import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import Main from "./main";
import {cleanSL, initSellLeadersAC, requestItemsSlider, requestItemsFromCategories} from "../../redux/productsReducer";
import {requestLastPurchases} from "../../redux/lastPurchasesReducer";

let mapStateToProps = (state) => {

    return {
        slidesResult: state.SlidesPage.slidesResult,
        initSlider: state.SlidesPage.initSlider,
        initSellLeaders: state.SlidesPage.initSellLeaders,
        initLastPurchases: state.LastPurchasesPage.initLastPurchases
    }
}

export default compose(connect(mapStateToProps, {requestLastPurchases, requestItemsFromCategories, initSellLeadersAC, cleanSL, requestItemsSlider})) (Main);