import React from "react";
import CooperationImg from "../images/cooperationImg.jpg";
import style from "./Cooperation.module.css";
import IconAndDescription from "../mainStyles/IconAndDescription";
import VkImg from "../images/vk.png";
import emailImg from "../images/email.jpg"

const Cooperation = (props) => {
  return <div className="container-lg center">
      <img className={style.imgCooperation} src={CooperationImg} />
      <span className={style.cooperationText}>Если вы разработчик, блогер, обзорщик или стример, то мы можем рассмотреть ваше предложение для сотрудничества.
        Детально опишите ваши условия и отправьте на почтовый ящик 11111 или в сообщения группы Вконктакте 22222.
        Мы всегда рады новым предложениям и хорошим партнерам!</span>
      <ul className={style.wrapElementsCooperation}>
          <IconAndDescription element={<img className={style.imgCooperationLink} src={emailImg} />} description={"dsd"}/>
          <IconAndDescription element={<img className={style.imgCooperationLink} src={VkImg}/>} description={"sds"}/>
      </ul>
  </div>
}
export default Cooperation;