import React from "react";
import style from "./contacts.module.css";
import IconAndDescription from "../mainStyles/IconAndDescription";
import {AiOutlineForm} from "@react-icons/all-files/ai/AiOutlineForm";
import {GrGroup} from "@react-icons/all-files/gr/GrGroup";
import {AiOutlineMail} from "@react-icons/all-files/ai/AiOutlineMail";

const Contacts = (props) => {
  return <div className="container-lg center">
      <div className={style.wrapContacts}>
        <h3>Контакты поддержки</h3>
          <span>С нами Вы можете связаться несколькими способами. Описывайте проблему подробно, чтобы консультант поддержки оперативно вошел в курс дела вашей проблемы.
          Поддержка работает круглосуточно, но для ответа может потребоваться время, взависимости от наплыва обращений.</span>
          <ul className={style.wrapElementsContacts}>
              <IconAndDescription element={<AiOutlineForm/>}
                                  description={"Справа есть форма, при клике на которую откроется окно с тех. поддержкой"}/>
              <IconAndDescription element={<GrGroup/>} description={"У нас есть группа Вконтакте. Зайдя в неё, вы можете написать в сообщения"} />
              <IconAndDescription element={<AiOutlineMail/>} description={"Так же нам можно написать на почтовый ящик - "} />
          </ul>

      </div>
  </div>
}

export default Contacts;