import React, {useEffect, useState} from "react";
import {useParams} from "react-router-dom";
import axios from "axios";
import style from "./item.module.css";
import Carousel from "react-multi-carousel";
import {BsHandThumbsDown, BsHandThumbsUp, MdOutlineSell} from "react-icons/all";
import {FaCommentDots} from "@react-icons/all-files/fa/FaCommentDots";
import {RiErrorWarningLine} from "@react-icons/all-files/ri/RiErrorWarningLine";
import ItemNoAvailable from "./itemNoAvailable";

const Item = (props) => {

    const itemId = useParams().id
    const [itemImg, setItemImg] = useState('');
    const [cart, setCart] = useState('');
    const [promoCode, setPromoCode] = useState('');
    const [email, setEmail] = useState('');
    const [dirtyEmail, setDirtyEmail] = useState(false)
    const [emailError, setEmailError] = useState('Некорректный E-mail');
    const [formPayValid, setFormPayValid] = useState(false);
    const [countItem, setCountItem] = useState(1);
    const [dataProduct, setDataProduct] = useState(false);
    const [imagesSlider, setImagesSlider] = useState(false);
    const [items, setItems] = useState(false);
    const [dynamicItems, setDynamicItems] = useState();
    const [countItems, setCountItems] = useState(15);
    const [steamLogin, setSteamLogin] = useState('');
    const [IPUser, setIPUser] = useState('')

    useEffect(() => {
        if (emailError !== '') {
            setFormPayValid(false)
        } else {
            setFormPayValid(true)
        }
    }, [emailError])

    const getData = async () => {
        const ipRes = await axios.get('https://geolocation-db.com/json/')
        console.log(await ipRes)
        // const ipRes = await fetch('https://api.db-ip.com/v2/free/self')
        // let data = await ipRes.json()
        // console.log(await data)
        // setIPUser(res.data.IPv4)
        // let xhr = new XMLHttpRequest();
        // xhr.open('GET', 'https://api.ipify.org?format=json', true);
        // xhr.send();
        //
        // xhr.addEventListener('readystatechange', processRequest, false);
        //
        // function processRequest() {
        //     if (xhr.readyState === 4 && xhr.status === 200) {
        //         let response = JSON.parse(xhr.responseText);
        //        console.log(response)
        //     }
        // }

    }


    useEffect( () => {
        getData()

        axios.get(`https://api.digiseller.ru/api/products/${itemId}/data?currency=RUB`).then(response => {
            let data = response.data;
            console.log(data)
            if (data.retval === 3) {
                setDataProduct(3)
            } else {
                setItemImg(`https://graph.digiseller.ru/img.ashx?id_d=${itemId}`);
                setDataProduct(data.product)
                setImagesSlider(data.product.preview_imgs)
                console.log(data.product)
                document.getElementById("descriptionText").innerHTML = data.product.info
                axios.get(`https://api.digiseller.ru/api/reviews?seller_id=${data.product.seller.id}&product_id=${itemId}&type=all&rows=999`, {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                }).then(r => {
                    setItems(r.data.review)
                    let tempArrayReviews = []
                    for (let i = 0; i < countItems; i++) {
                        if (r.data.review[i] === undefined) break
                        else {
                            tempArrayReviews.push(r.data.review[i])
                        }
                    }
                    setDynamicItems(tempArrayReviews)
                })
            }
        })
    }, [])




    let goToPay = () => {
        if (formPayValid) {
            function addToCart () {
                let xhrLink = `product_id=${itemId}&product_cnt=${countItem}&email=${email}&lang=ru-RU&id_po=${poResult}`;
                axios.post('//shop.digiseller.ru/xml/shop_cart_add.asp',
                    xhrLink,
                    {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }).then(res => {
                    console.log(res);
                    setCart(res.data.cart_uid);
                    axios.post('//shop.digiseller.ru/xml/shop_cart_lst.asp',
                        `cart_uid=${res.data.cart_uid}&lang=ru-RU`,
                        {
                            'Content-Type': 'application/x-www-form-urlencoded'
                        }).then(res2 => {
                            console.log(res2);
                            document.payForm.target = "_blank"
                            document.payForm.submit()
                        }
                    ).catch(err => {
                        console.log(err)
                    })
                }).catch(err => {
                    console.log(err)
                });
            }
            let poResult = '';
            if (dataProduct.options) {
                let id_po = {
                    "product_id": itemId,
                    "options": [
                        {
                            "id": 194031,
                            "value": {"text": steamLogin}
                        }
                    ],
                    "unit_cnt": countItem,
                    "lang": "ru-RU",
                    "ip": IPUser
                }
                axios.post('https://api.digiseller.ru/api/purchases/options',
                    id_po,
                    {
                        "Accept": "application/json",
                        "Content-Type": "application/json"
                    }
                ).then(resPO => {
                    poResult = resPO.data.id_po
                    addToCart()
                })

            } else {
                addToCart()
            }
        }
    }

    // EDIT COUNT ITEM
    let plusItem = () => {
        setCountItem(countItem + 1)
    }
    let minusItem = () => {
        if (countItem != 1 && countItem > 0) {
            setCountItem(countItem - 1)
        }
    }

    let changeCountItem = (e) => {
        if (Number(e.target.value) > 0) {
            setCountItem(Number(e.target.value))
        }}

   const setMainImg = (event) => {
        setItemImg(event.target.src)
   }
   const onChangeEmail = (e) => {
        setEmail(e.target.value)

       if (!String(e.target.value)
           .toLowerCase()
           .match(
               /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
           )) {
           setEmailError("Некорректный E-mail")
       } else {
           setEmailError('')
       }
   }
const onChangeLogin = (e) => {
    setSteamLogin(e.target.value)
}

   const blurHandler = () => {
     setDirtyEmail(true)
   }

   const downloadReviews = () => {
       let lastCountReviews = countItems + 15
       setCountItems(lastCountReviews)
       let tempArrayReviews = []
       for (let i = 0; i < lastCountReviews; i++) {
           if (items[i] === undefined) break
           else {
               tempArrayReviews.push(items[i])
           }
       }
       setDynamicItems(tempArrayReviews)
   }

    return  <>
    {dataProduct === 3 ? <ItemNoAvailable /> : <div className="container-lg center">
        <div  className="row">
            <div style={{
                padding: 0,
                margin: 0
            }} className="col-lg-6">
                <span className={style.nameItem}>{dataProduct.name}</span>
                <div>
                    <img className={style.mainImageItem} src={itemImg} />
                </div>
                <div>
                    <Carousel
                        renderButtonGroupOutside
                        additionalTransfrom={0}
                        draggable
                        focusOnSelect={false}
                        infinite
                        minimumTouchDrag={5}
                        rewind={false}
                        rewindWithAnimation={false}
                        rtl={false}
                        shouldResetAutoplay
                        slidesToSlide={1}
                        swipeable
                        centerMode={true}
                        arrows={true}
                        responsive={{
                            desktop: {
                                breakpoint: {
                                    max: 3000,
                                    min: 1397
                                },
                                items: 3,

                            },
                            desktop2: {
                                breakpoint: {
                                    max: 1397,
                                    min: 1024
                                },
                                items: 2,

                            },
                            tablet: {
                                breakpoint: {
                                    max: 1024,
                                    min: 992
                                },
                                items: 2,

                            },
                            tablet2: {
                                breakpoint: {
                                    max: 991,
                                    min: 780
                                },
                                items: 4,

                            },
                            mobile: {
                                breakpoint: {
                                    max: 779,
                                    min: 680
                                },
                                items: 3,

                            },
                            mobile2: {
                                breakpoint: {
                                    max: 679,
                                    min: 0
                                },
                                items: 2,
                            }
                        }}
                    >
                        {imagesSlider && imagesSlider.length > 1 && imagesSlider.map(item =>
                            <img key={item.id} onClick={(event) => {setMainImg(event)}} className={style.imgItemStyle} src={item.url} />

                        )}
                    </Carousel>
                </div>
            </div>
            <div className="col-lg-6">

                <div className={style.wrapSecondBlock}>

                        <span className={style.countSells}>Продаж: {dataProduct && dataProduct.statistics.sales} <MdOutlineSell /></span><br/>
                        <span className={style.sellerNick}>Продавец {dataProduct && dataProduct.seller.name}</span>
                        <br/>
                            <div className={style.wrapInfoBlock}>
                                <div className={style.countBtns}>
                                    <span className={style.buttonCount} onClick={plusItem}>+</span>
                                    <span className={style.buttonCount} onClick={minusItem}>-</span>
                                </div>
                                <input className={style.countWindow} onChange={changeCountItem} type="text" value={countItem}/>
                            </div>
                            <div className={style.wrapInfoBlock}>
                                <div className={style.priceItem}>{dataProduct && dataProduct.price} ₽</div>
                            </div>

                        <div className={style.blockBuyAndPromoCode}>
                            <input className="form-control" onChange={(e) => {
                                setPromoCode(e.target.value)
                            }} type="text" placeholder={"Промокод"} value={promoCode}/>
                            <br/>

                             {/*Email user*/}
                            <div className="mb-3">
                                {dataProduct.options && <>
                                    <label htmlFor="exampleInputEmail2" className="form-label">Логин Steam аккаунта</label>
                                    <input onChange={onChangeLogin} type="login" className="form-control" id="exampleInputEmail2"
                                           placeholder="Введите ваш логин от Steam" aria-describedby="emailHelp" value={steamLogin} />
                                </>}

                                <label htmlFor="exampleInputEmail1" className="form-label">Ваш E-mail</label>
                                <input onBlur={blurHandler} onChange={onChangeEmail} type="email" className="form-control" id="exampleInputEmail1"
                                       placeholder="Обязательное поле" aria-describedby="emailHelp" value={email} />
                                {dirtyEmail && emailError && <div id="emailHelp" className="form-text">{emailError}</div>}
                            </div>
                            <button className={style.buttonBuy}
                                    formTarget="_blank" type="submit" onClick={goToPay}>Купить</button>
                        </div>
                </div>
            </div>
        </div>

        <div>
            <span className={style.descriptionTitle}>Описание товара</span>
            <span id="descriptionText"></span>
        </div>

        <form formTarget="_blank" id="payForm" name="payForm" action="https://oplata.info/asp2/pay.asp" method="post">
            <input type="hidden" name="cart_uid" value={cart} />
            <input type="hidden" name="agent" value="549328" />
            <input type="hidden" name="promocode" value={promoCode} />
            <input type="hidden" name="email" value={email} />
            <input type="hidden" name="failpage" value="http://localhost:3000/" />
        </form>

        <div className={style.wrapReviewItem}>
            <span>Отзывы о товаре</span>
            <span className={style.countReview}><span
                className={style.thumbGood}><BsHandThumbsUp/></span>{dataProduct && dataProduct.statistics.good_reviews}</span>
            <span className={style.countReview}><span
                className={style.thumbBad}><BsHandThumbsDown/></span>{dataProduct && dataProduct.statistics.bad_reviews}</span>
            <div className={style.reviewWarning}>
                <div className={style.reviewWarningIcon}><RiErrorWarningLine/></div>
                <div className={style.reviewWarningText}>Просьба не оставлять негативный отзыв до связи с
                    продавцом
                </div>
            </div>
        </div>

        {!!dynamicItems && dynamicItems.map(review =>
            <div key={review.id} className={style.review}>
                <div className="row">
                    <div className="col-lg-9">
                        <div className={style.wrapImgAndTextReview}>
                            <div className={style.reviewImgWrap}>
                                <span className={review.type === "good" ? style.goodBgReview : style.badBgReview}><FaCommentDots/></span>
                            </div>
                            <div>
                                {review.info}
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3">
                        <div className={style.reviewBlockDate}>
                            {review.date}
                        </div>
                    </div>
                </div>
            </div>
        )}

        {countItems < items.length && <button className={style.buttonDownloadReviews} onClick={downloadReviews}>Подгрузить ещё</button>}
    </div>}



    </>

}

export default Item;