import React from "react";
import style from "./guarantee.module.css";
import {GoBook} from "@react-icons/all-files/go/GoBook";
import {CgKey} from "@react-icons/all-files/cg/CgKey";
import {BiSupport} from "@react-icons/all-files/bi/BiSupport";
import {RiScalesLine} from "@react-icons/all-files/ri/RiScalesLine";
import {BsBank2, GrCertificate} from "react-icons/all";
import {VscRocket} from "@react-icons/all-files/vsc/VscRocket";
import {SiWebmoney} from "@react-icons/all-files/si/SiWebmoney";
import IconAndDescription from "../mainStyles/IconAndDescription";


const Guarantee = (props) => {
  return <div className="container-lg center">
      <div className={style.wrapGuarantee}>
          <h3>Гарантии и информация о нас</h3>

          <ul className={style.wrapElementsGuarantee}>
              <IconAndDescription element={<GoBook />}
                                  description={"Десятки тысяч положительных отзывов о наших товарах. Мы делаем все, чтобы покупатель остался доволен!"} />
              <IconAndDescription element={<CgKey />}
                                  description={"Покупая лицензионный ключ, вы можете быть уверены в том, что он получен легальным путем. Все ключи закупаются у официальных разработчиков."} />
              <IconAndDescription element={<BiSupport/>}
                                  description="В любое время суток Вы можете обратиться в нашу тех. поддержу, в которой вам помогут с возникшей проблемой."/>
              <IconAndDescription element={<RiScalesLine/>}
                                  description="Нашим приоритетом является стабильно предоставлять качественный товар."/>
              <IconAndDescription element={<BsBank2/>}
                                  description="Все платежи принимаются крупными платежными агрегаторами. Вы можете не сомневаться в безопасноти транзакций."/>
              <IconAndDescription element={<VscRocket/>}
                                  description="Моментальная доставка товара. После оплаты, товар приходит сразу вам на почту."/>
              <IconAndDescription element={<GrCertificate/>}
                                  description="Мы используем сертификат SSL в соответствии с требованиями безопасности."/>
              <IconAndDescription element={<SiWebmoney/>}
                                  description="Пройдена официальная аттестация и получен сертификат от платежного агрегатора WebMoney."/>
          </ul>
      </div>
  </div>
}

export default Guarantee;